import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			name: ''
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//保存昵称
		save() {
			this.$api.request.editInfo({
				userUuid: this.userInfo.userUuid,
				actionType: 'NAME',
				name: this.name
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.$api.msg('信息已保存');
					setTimeout(function() {
						uni.switchTab({
							url: '/pages/user/user'
						})
					}, 1000);
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			});
		},

	},
	mounted() {
		this.name = this.userInfo.name;
	}
}
